import React, { Component } from 'react';
import { H1, H2, H5, P, Subtitle, Transition, NextBtn } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Br, TwoCol } from '../css/container.js';
import { srContent } from '../content.js';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Image from '../components/Image';
import Tools from '../components/Tools';

class ScatteringResources extends Component {
  render() {
    return (
      <div>
        <Navbar/>
        <InnerContainer>
          <br/><br/><br/><br/>
          <br/><Br/><Br/>
          <H1 large center>{srContent.title}</H1>
          <br/><Br/><Br/><Br/>
          <img src={srContent.header} alt={srContent.header} style={{"width":"100%"}}/>
          <br/><br/><Br/><Br/><Br/>
          <TwoCol>
            <P right dangerouslySetInnerHTML={{ __html: srContent.other }}/>
            <H5 dangerouslySetInnerHTML={{ __html: srContent.role }}/>
          </TwoCol>
          <br/><br/>
          <Subtitle>CLIENT REQUEST</Subtitle>
          <H2>Convert website visitors to donors and supporters of SR</H2>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.context }} />
          <br/>
            <Subtitle>DEFINE SUCCESS</Subtitle>
            <H2>To ensure our work will bring value to SR,</H2>
            <H5 dangerouslySetInnerHTML={{ __html: srContent.success }} />
            <img src={srContent.successimg} alt={srContent.successimg} style={{"width":"100%"}}/><br/><br/><br/>

        </InnerContainer>

        <OuterContainer>
          <InnerContainer>

              {/**}<H2>PROCESS</H2>
              <img src={srContent.process} alt={srContent.process} style={{"width":"100%"}}/>
              <br/><br/><br/><br/>**/}


                <br/><br/>
                <Transition dangerouslySetInnerHTML={{ __html: srContent.transition }} />

                <br/><br/><br/><br/><br/>

              <Subtitle>UX AUDIT</Subtitle>
              <H2>First, we identified the problems of the current website</H2>
              <div>
                <H5 dangerouslySetInnerHTML={{ __html: srContent.audit }} />
                <img src={srContent.uaimg} alt={srContent.uaimg} style={{"width":"100%"}}/><br/>
              </div>
              <br/><br/>

              <Subtitle>WIREFRAMES</Subtitle>
              <H2>We then dived into brainstorming alternatives of the landing page</H2>
              <H5 dangerouslySetInnerHTML={{ __html: srContent.home }}/><br/>
              <img src={srContent.homeimg1} alt={srContent.homeimg1} style={{"width":"48%"}}/>
              <img src={srContent.homeimg2} alt={srContent.homeimg2} style={{"width":"48%"}}/>

              <br/><br/><br/><br/>

              <Subtitle>USER INTERVIEW</Subtitle>
              <H2>To further understand the vision of users and stakeholders about the website, </H2>
              <div>
                <H5 dangerouslySetInnerHTML={{ __html: srContent.interview }} />
                <img src={srContent.interviewimg} alt={srContent.interviewimg} style={{"width":"100%"}}/>
              </div>
              <br/><br/><br/>

              <H2>To be continued ...</H2>
              <H5 dangerouslySetInnerHTML={{ __html: srContent.nextstep }} />
          </InnerContainer>
        </OuterContainer>
        <NextBtn to={srContent.link}>
          <div dangerouslySetInnerHTML={{ __html: srContent.action }} />
        </NextBtn>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default ScatteringResources;
